import React, {Component} from 'react';
import ContentPane from '../../containers/ContentPane';
import {registerNewUser} from '../../util/data/connect';
import {Button} from 'semantic-ui-react'

class Registration extends Component{


    constructor(props){
        super(props);

        this.state = {
            settings: {
                firstname: '',
                lastname: '',
                age: '',
                email: '',
                password: '',
            }
        }
    }

    handleChange = (e) => {
        switch(e.target.id){
            default:
                this.setState({
                    settings: {
                        ...this.state.settings,
                        [e.target.id]: e.target.value
                    }
                });
        }
    }

    readyToSubmit = () => {
        return Object.keys(this.state.settings).some(element => {
            return element.length < 1;
        })
    }

    generateInputs = (inputFields) => {
        const fields = inputFields.map(field => {
            const type = field === 'Password' ? 'password' : 'text'
            return (
                <tr>
                    <td>
                        <label style={{marginRight: '1em'}}>{field}</label>
                    </td>
                    <td>
                        <input
                        type={type}
                        id={field}
                        value={this.state.settings[field]}
                        onChange={(e) => this.handleChange(e)}/>
                    </td>
                </tr>
            );
        });


        return (
            <table>
                {fields}
            </table>
        );
    }

    register = async() => {
        const response = await registerNewUser(this.state.settings)
        if(response['success']) {
            this.props.toggleRegistration();
        } else if(response['error']) {
            this.props.errorReceived(response['error']);
        }
    }

    registrationScreen = () => {
        return (
            <div style={{height: '100%'}}>
                <div className="registration-fields">
                    {this.generateInputs(Object.keys(this.state.settings))}
                </div>
                <div className="login-controls">
                    <Button style={{marginRight: '1em'}} disabled={this.readyToSubmit()} onClick={() => this.register()}>Register</Button>
                    <Button onClick={() => this.props.toggleRegistration()}>Cancel</Button>
                </div>
            </div>
        );
    }

    render(){
        return(
            <ContentPane>
                {this.registrationScreen()}
            </ContentPane>
        );
    }
}

export default Registration;


