import {MONGODB_BASE_URL} from '../constants';

export const getUserID = (email, password) => {
    const url = MONGODB_BASE_URL + '/user/login';

    let payload = {
        "credentials": {
            "email": email,
            "password": password
        }
    }
    payload = JSON.stringify(payload);

    const options = {
        method: 'POST',
        headers: {
            "Content-Type": "application/json"
        },
        body: payload
    };

    return fetch(url, options)
    .then(response => response.json())
    .catch(error => console.log('error', error))
    .then(data => data);
};

export const registerNewUser = (user) => {
    const url = MONGODB_BASE_URL + '/user/register';

    let payload = {
        settings: user
    };
    payload = JSON.stringify(payload);

    const options = {
        method: 'POST',
        headers: {
            "Content-Type": "application/json"
        },
        body: payload
    };

    return fetch(url, options)
    .then(response => response.json())
    .catch(error => console.log('error', error))
    .then(data => data);
};

export const checkConnection = async () => {
    const url = MONGODB_BASE_URL + '/active';

    const options = {
        method: 'GET'
    }

    let active = false;
    await fetch(url, options)
    .then(response => response.json())
    .catch(error => {
        active = error
    })
    .then(data => {
        active = data
    });

    return active ? (active['success'] ? true : false) : false;
};

export const getUserSettings = (userId) => {
    const url = MONGODB_BASE_URL + '/user/' + userId;
    const options = {
        method: 'GET'
    };


    return fetch(url, options)
    .then(response => response.json())
    .catch(error => console.log('error', error))
    .then(data => data);
}

export const setUserSettings = (user) => {
    const url = MONGODB_BASE_URL + '/user/' + user.userId;
    let payload = user;

    console.log('payload', payload)
    payload = JSON.stringify(payload);
    const options = {
        method: 'PUT',
        headers: {
            "Content-Type": "application/json"
        },
        body: payload
    };

    return fetch(url, options)
    .then(response => response.json())
    .catch(error => console.log('error', error))
    .then(data => data);

}


