import React, {Component} from 'react';
import ContentPane from '../../containers/ContentPane';

class HomeScreen extends Component {

    constructor(props){
        super(props);

        this.state = {
            settings: props.user.settings
        }
    }

    render(){
        return (
            <ContentPane>
                <div>
                    <label>{'Welcome ' + (this.state.settings.firstname || this.state.settings.FirstName)}</label>
                </div>
            </ContentPane>
        );
    }
}

export default HomeScreen;


