import React, {Component} from 'react';
import ContentPane from '../../containers/ContentPane';
import {Button, Header, Icon} from 'semantic-ui-react';

class Settings extends Component{


    constructor(props){
        super(props);

        this.state = {
            fields: Object.keys(props.settings),
            settings: {...props.settings}
        }
    }

    handleChange = (e) => {
        switch(e.target.id){
            default:
                this.setState({
                    settings: {
                        ...this.state.settings,
                        [e.target.id]: e.target.value
                    }
                });
        }
    }

    generateInputs = (inputFields) => {
        const fields = inputFields.map(field => {
            const type = ['password', 'Password'].includes(field) ? 'password' : 'text'
            return (
                <tr key={field}>
                    <td>
                        <label style={{marginRight: '1em'}}>{field}</label>
                    </td>
                    <td>
                        <input
                        type={type}
                        id={field}
                        value={this.state.settings[field]}
                        onChange={(e) => this.handleChange(e)}/>
                    </td>
                </tr>
            );
        });


        return (
            <table>
                <tbody>
                    {fields}
                </tbody>
            </table>
        );
    }

    settingsScreen = () => {
        return (
            <div style={{height: '100%'}}>
                <div className="settings-fields">
                    {this.generateInputs(this.state.fields)}
                </div>
                <div className="settings-controls">
                    <Button style={{marginRight: '1em'}} onClick={() => this.props.setSettings(this.state.settings)}>Save</Button>
                    <Button icon="settings" >Preferences</Button>
                </div>
            </div>
        );
    }

    render(){
        return(
            <ContentPane>
                <Header as='h3'>
                    <Icon name='settings' />
                    <Header.Content>
                        User Settings
                        <Header.Subheader>Manage Your Settings</Header.Subheader>
                    </Header.Content>
                </Header>
                {this.settingsScreen()}
            </ContentPane>
        );
    }
}

export default Settings;


