import React, {Component} from 'react';
import './styles.css';
import Settings from '../components/Settings/Settings';
import Login from '../components/Login/Login';
import HomeScreen from '../components/Home/HomeScreen';

import {DEFAULT_ERROR_MESSAGE} from '../util/constants';
import {
    checkConnection, 
    getUserSettings, 
    setUserSettings
} from '../util/data/connect';

import {Button, Icon} from 'semantic-ui-react'

const initialState = {
    settingsMode: false,
    user_id: '',
    error: {
        showError: false,
        message: DEFAULT_ERROR_MESSAGE
    },
    settings: {}
}

class Melody extends Component {

    constructor(props){
        super(props);

        this.state = {
            settingsMode: false,
            user_id: '',
            error: {
                showError: false,
                message: DEFAULT_ERROR_MESSAGE
            },
            settings: {}
        }

        setInterval(async () => {
            if(await checkConnection() === false){
                this.toggleShowError("Network Error");
            }
        }, 20000);
    }

    toggleSettings = () => {
        this.setState({
            settingsMode: !this.state.settingsMode
        });
    }

    setID = async (user_id) => {
        const userSettings = await getUserSettings(user_id);
        if(userSettings === undefined) {
            this.toggleShowError()
        }
        this.setState({
            user_id: user_id,
            settings: {
                ...this.state.settings,
                ...userSettings.user.settings
            }
        });
    }

    logout = () => {
        this.setState(initialState);
    }

    setSettings = async (settings) => {
        console.log('settings', settings);
        const user = {
            user_id: this.state.user_id,
            settings: settings
        }
        const response = await setUserSettings(user);
        if (response.success) {
            this.setState({
                settings: settings,
                settingsMode: false
            });
        } else if (response.error) {
            this.toggleShowError(response.error)
        }
        
    }

    mainScreen = () => {
        const user = {
            user_id: this.state.user_id,
            settings: {
                ...this.state.settings
            }
        }
        return (
            <React.Fragment>
                {
                    this.state.settingsMode 
                    ? <Settings 
                        setSettings={this.setSettings}
                        settings={this.state.settings}
                        /> 
                    : <HomeScreen user={user}/>
                }
            </React.Fragment>
        );
    }

    errorBanner = () => {
        return (
            <div className="error-display">
                <b>
                    <label className="error-message">{"Error: " + this.state.error.message}</label>
                </b>
            </div>
        );
    }

    toggleShowError = (message=DEFAULT_ERROR_MESSAGE) => {
        this.setState({
            error: {
                showError: !this.state.error.showError,
                message: message
            }
        });
    }

    errorReceived = (message) => {
        this.toggleShowError(message);
        setTimeout(() => this.toggleShowError(), 5000);
    }

    render(){
        return(
            <div className="mainview">
                {this.state.error.showError && this.errorBanner()}
                <div className="navigation">
                    <div className="return-home">
                        {
                            this.state.user_id.length > 0 &&
                            <Button onClick={() => this.logout()}>
                                <Icon name='sign-out'/>
                            </Button>  
                        } 
                    </div>
                    <div className="settings">
                        {this.state.user_id.length > 0 && 
                        <Button onClick={() => this.toggleSettings()}>
                            <Icon name="setting"/>
                        </Button>}
                    </div>
                </div> 
                {this.state.user_id.length > 0 
                    ? this.mainScreen() 
                    : <Login 
                    setID={this.setID.bind(this)} 
                    errorReceived={this.errorReceived.bind(this)}
                    />}
            </div>
        );
    }
}

export default Melody;


