import React, {Component} from 'react';
import ContentPane from '../../containers/ContentPane';
import {getUserID} from '../../util/data/connect';
import Registration from './Registration';
import './styles.css'

import {Button, Input} from 'semantic-ui-react';
 
class Login extends Component {

    state = {
        email: '',
        password: '',
        showRegistration: false
    }

    handleChange = (e) => {
        switch(e.target.id){
            case 'email':
                this.setState({email: e.target.value});
                break;
            case 'password':
                this.setState({password: e.target.value});
                break;
        }
    }

    credentialFields = () => {
        return (
            <tbody>
                <tr>
                    <td>
                        <label style={{marginRight: '1em'}}>Email</label>
                    </td>
                    <td>
                        <Input
                        iconPosition='left' 
                        icon='mail'
                        type='text'
                        id='email'
                        value={this.state.email}
                        onChange={e => this.handleChange(e)}/>
                    </td>
                </tr>

                <tr>
                    <td>
                        <label style={{marginRight: '1em'}}>Password</label>
                    </td>
                    <td>
                        <Input
                        iconPosition='left'
                        icon='key'
                        type='password'
                        id='password'
                        value={this.state.password}
                        onChange={e => this.handleChange(e)}/>
                    </td>
                </tr>
            </tbody>
        );
    }

    loadFromDB = async () => {
        const userID = await getUserID(this.state.email, this.state.password);
        if(!userID['error']) {
            this.props.setID(userID['user_id']);
        } else {
            this.props.errorReceived(userID['error'])
        }
    }

    toggleRegistration = () => {
        this.setState({
            showRegistration: !this.state.showRegistration
        });
    }

    loginView = () => {
        return (
            <React.Fragment>
                 <React.Fragment>
                    <div className="logo-container">
                        <img src="/images/logo.png" className="logo"/>
                    </div>
                    <div className="title-container">
                        <div className="title">
                            <b><label style={{fontSize: '150%'}}>Melody</label></b>
                            <label style={{marginTop: '2em', marginLeft: '20%'}}>Login</label>
                        </div>
                    </div>
                </React.Fragment>
                <div className="login">
                    <div className="credentials">
                        <table>
                            {this.credentialFields()}
                        </table>
                    </div>
                    <div className="login-controls">
                        <Button style={{marginRight: '1em'}} onClick={() => this.loadFromDB()}>Login</Button>
                        <Button onClick={() => this.toggleRegistration()}>New User</Button>
                    </div>
                </div>
            </React.Fragment>
        );
    }

    render(){
        return(
            <React.Fragment>
                {!this.state.showRegistration 
                    ? (<ContentPane logo={true}>
                    {this.loginView()}
                    </ContentPane>) 
                    : <Registration 
                     errorReceived={this.props.errorReceived} 
                    toggleRegistration={this.toggleRegistration}/>
                }
            </React.Fragment>
        );
    }
}

export default Login;


